// src/theme.js
import { createTheme } from '@mui/material/styles';
import { CssBaseline } from '@mui/material';

const theme = createTheme({
  palette: {
    primary: {
      main: '#EA462B',
    },
    background: {
      default: '#EA462B',
    },
    text: {
      primary: '#423838',
    },
  },
  components: {
    MuiCssBaseline: {
      styleOverrides: `
        body {
          -webkit-font-smoothing: antialiased;
          -moz-osx-font-smoothing: grayscale;
          text-rendering: optimizeLegibility;
          background-color: #EA462B; // Apply your theme's background color here
        }
      `,
    },
  },
  typography: {
    fontFamily: 'Roboto, Arial, sans-serif', // Choose a Google Font that suits your style
  },
});

export default theme;
