import React, { useState, useEffect } from 'react';
import { Card, CardContent, Typography, Grid, useMediaQuery, useTheme, LinearProgress } from '@mui/material';

const MagmaPhunksGallery = () => {
    const [nfts, setNfts] = useState([]);
    const [loading, setLoading] = useState(true);
    const [error, setError] = useState(null);
    const [fetchProgress, setFetchProgress] = useState(0);

    const theme = useTheme();
    const isMobile = useMediaQuery(theme.breakpoints.down('sm'));

    useEffect(() => {
        async function fetchData() {
            console.log("Fetching NFT data...");
            try {
                let allNfts = []; // Store all fetched NFTs
                let pageCount = 0; // Counter for the number of pages fetched
                let uniqueToken = null; // Initially, there's no unique token
                const pageSize = 50; // You can adjust this as needed

                while (true) {
                    let apiUrl = `https://magmascan.org/api/v2/tokens/0x7224E81c03429Da161634e68F4F64cFE11b37E0d/instances?limit=${pageSize}`;
                    if (uniqueToken) {
                        apiUrl += `&unique_token=${uniqueToken}`; // Append the unique token if it exists
                    }

                    console.log("Fetching page:", apiUrl);

                    const response = await fetch(apiUrl);
                    if (!response.ok) {
                        throw new Error(`API call failed with status: ${response.status}`);
                    }

                    const data = await response.json();
                    if (!data.items || data.items.length === 0) {
                        console.log("No more items fetched, exiting loop.");
                        break; // Exit the loop if no items are fetched
                    }

                    allNfts = allNfts.concat(data.items); // Add fetched items to the allNfts array
                    pageCount++; // Increment the page count

                    if (!data.next_page_params || data.next_page_params.unique_token === null) {
                        console.log("No more pages, exiting loop.");
                        break; // Exit the loop if there are no more pages
                    }

                    uniqueToken = data.next_page_params.unique_token; // Update the unique token for the next iteration

                    // Update fetch progress based on the number of pages fetched
                    const progress = (pageCount / 200) * 100; // Assuming 200 pages in total (10000 / 50)
                    setFetchProgress(progress);
                }

                console.log("NFT data fetched successfully:", allNfts);
                setNfts(allNfts);
            } catch (err) {
                console.error('Error fetching NFT data:', err);
                setError(`Failed to load NFT data: ${err.message}`);
            } finally {
                setLoading(false);
            }
        }

        fetchData();
    }, []);

    const handleImageClick = (nft) => {
        console.log(`Image for ${nft.id} clicked for download`);
        // Implement download functionality here
    };

    const renderImage = (nft) => {
        if (nft.image_url && typeof nft.image_url === 'string') {
            if (nft.image_url.startsWith('data:image/svg+xml')) {
                const decodedSVG = decodeURIComponent(nft.image_url.split(',')[1]);
                return <img src={`data:image/svg+xml;utf8,${decodedSVG}`} alt={`MagmaPhunk ${nft.id}`} style={{ width: '100%', height: 'auto' }} onClick={() => handleImageClick(nft)} />;
            } else {
                return <img src={nft.image_url} alt={`MagmaPhunk ${nft.id}`} style={{ width: '100%', height: 'auto' }} onClick={() => handleImageClick(nft)} />;
            }
        } else {
            return <div>No image available</div>;
        }
    };

    if (loading) return (
        <div>
            <LinearProgress variant="determinate" value={fetchProgress} />
            <Typography variant="h6" style={{ marginTop: 10 }}>
                Fetching Phunks... {isNaN(fetchProgress) ? 0 : Math.floor(fetchProgress)}%
            </Typography>
        </div>
    );
    if (error) return <div>Error: {error}</div>;

    return (
        <div style={{ padding: '20px' }}>
            <Grid container spacing={1}>
                {nfts.map(nft => (
                    <Grid item key={nft.id} xs={4} sm={4} md={4}>
                        <Card style={{ background: 'none', boxShadow: 'none', textAlign: 'center' }}>
                            <CardContent>
                                <Typography variant="h5" component="div">
                                    {nft.id}
                                </Typography>
                                {renderImage(nft)}
                            </CardContent>
                        </Card>
                    </Grid>
                ))}
            </Grid>
        </div>
    );
};

export default MagmaPhunksGallery;
