import React, { useState, useEffect } from 'react';
import { BrowserRouter as Router, Route, Routes } from 'react-router-dom';
import { ThemeProvider, Snackbar, Alert, Card, CardContent, Typography, Link, Box, Tooltip, IconButton } from '@mui/material';
import ContentCopyIcon from '@mui/icons-material/ContentCopy';
import Web3 from 'web3';
import theme from './theme';
import CustomAppBar from './AppBar';
import MagmaPhunksGallery from './MagmaPhunksGallery';

function App() {
  const [web3, setWeb3] = useState(null);
  const [isConnected, setIsConnected] = useState(false);
  const [snackbarOpen, setSnackbarOpen] = useState(false);
  const [snackbarMessage, setSnackbarMessage] = useState('');
  const [snackbarSeverity, setSnackbarSeverity] = useState('info');

  const handleSnackbarClose = () => {
    setSnackbarOpen(false);
  };

  const connectWallet = async () => {
    if (window.ethereum) {
      try {
        const web3Instance = new Web3(window.ethereum);
        await window.ethereum.request({ method: 'eth_requestAccounts' });
        const accounts = await web3Instance.eth.getAccounts();
        if (accounts.length > 0) {
          setWeb3(web3Instance);
          setIsConnected(true);
          setSnackbarMessage(`Success, connected address: ${accounts[0]}, Network chain ID: ${await web3Instance.eth.net.getId()}`);
          setSnackbarSeverity('success');
        } else {
          throw new Error('No accounts found.');
        }
      } catch (error) {
        setSnackbarMessage(`Wallet connect failed: ${error.message}. Please try again.`);
        setSnackbarSeverity('error');
      } finally {
        setSnackbarOpen(true);
      }
    } else {
      setSnackbarMessage('No Ethereum wallet found. Please install MetaMask or another wallet.');
      setSnackbarSeverity('error');
      setSnackbarOpen(true);
    }
  };

  const disconnectWallet = () => {
    setWeb3(null);
    setIsConnected(false);
    setSnackbarMessage('Disconnected from wallet.');
    setSnackbarSeverity('info');
    setSnackbarOpen(true);
  };

  const Gallery = () => {
    const [data, setData] = useState(null);

    useEffect(() => {
      fetch('https://magmascan.org/api/v2/tokens/0x7224E81c03429Da161634e68F4F64cFE11b37E0d')
        .then(response => response.json())
        .then(data => setData(data))
        .catch(error => console.error('Error fetching data:', error));
    }, []);

    const handleCopyAddress = (event) => {
      event.preventDefault(); // Prevents page refresh
      navigator.clipboard.writeText(data.address).then(() => {
        setSnackbarMessage('Address copied to clipboard!');
        setSnackbarSeverity('success');
        setSnackbarOpen(true);
      }, () => {
        setSnackbarMessage('Failed to copy address.');
        setSnackbarSeverity('error');
        setSnackbarOpen(true);
      });
    };

    if (!data) {
      return <div>Loading...</div>;
    }

    return (
      <Card style={{ margin: '20px', backgroundColor: '#fff', padding: '20px', borderRadius: '0' }}>
        <CardContent>
          <Box style={{ display: 'flex', alignItems: 'center', marginBottom: '20px' }}>
            <Typography variant="h6" color="textPrimary" gutterBottom style={{ marginRight: '10px' }}>
              MagmaPhunks
            </Typography>
            <Typography variant="subtitle1" color="textSecondary">
              {data.symbol}
            </Typography>
          </Box>
          <Typography variant="body2" color="textPrimary" style={{ display: 'flex', flexDirection: 'column', alignItems: 'flex-start' }}>
            <span style={{ display: 'flex', alignItems: 'center', fontSize: '0.8rem' }}>
              {data.address}
              <Tooltip title="Copy Address">
                <IconButton onClick={handleCopyAddress} size="small" style={{ marginLeft: '5px' }}>
                  <ContentCopyIcon fontSize="small" />
                </IconButton>
              </Tooltip>
            </span>
          </Typography>
          <Typography variant="body2" color="textPrimary">
            <strong>Holders:</strong> {data.holders}
          </Typography>
          <Typography variant="body2" color="textPrimary">
            <strong>Total Supply:</strong> {data.total_supply}
          </Typography>
        </CardContent>
      </Card>
    );
  };

  return (
    <ThemeProvider theme={theme}>
      <Router>
        <Routes>
          <Route path="/" element={<Gallery />} />
        </Routes>
        <MagmaPhunksGallery />
        <Snackbar open={snackbarOpen} autoHideDuration={6000} onClose={handleSnackbarClose}>
          <Alert onClose={handleSnackbarClose} severity={snackbarSeverity} sx={{ width: '100%' }}>
            {snackbarMessage}
          </Alert>
        </Snackbar>
      </Router>
    </ThemeProvider>
  );
}

export default App;
